@import "../../sassHelpers/mixins";

.job {
  &__title {
    @include sectionTitle(rgb(110, 178, 255));
  }

  &__content {
    padding: 3rem 4rem;
    &-info {
      @include sectionInfo();
    }
  }

  &__application-finished {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    padding: 3rem;
    width: 80%;
    max-width: 50rem;
    box-shadow: 1px 2px 5px black;
    background-color: rgb(0, 204, 255);
    font-size: 2rem;
    z-index: 800;
  }
}